<!--  -->
<template>
<div class='classify'>
    <div class='slide'>
        <div :class="['slide-list',classify_index==index?'slide-active':'']" v-for='(item,index) in classify' :key='index' @click="navName(item.id,index)" >
            {{item.typeName}}
            <span class='slide-border' v-if='classify_index==index' ></span>
        </div>
    </div>
    <div class='classify-inner'>
        <div class='classify-inner-list' v-for='(item,index) in classifyList' :key='index' @click='listName(item.id)'>
            <img :src="item.pictureUrl" alt="" class='classify-img' >
            <p class='classify-name'>{{item.typeName}}</p>
        </div>
    </div>
</div>
</template>

<script>
    import {classifyNav,classifyInner} from '../../api/api';
    export default {
    data() {
        return {
           classify:[],
           classify_index:0,
           classifyList:[],
        };
    },
    computed: {
       
    },
    watch: {},
    methods: {
        navName(id,index){
            if(this.classify_index!=index){
                this.classify_index=index
                this.loading = this.$toast.loading();
                classifyInner(id).then(response=>{
                    this.loading.hide();
                    this.classifyList=response.data.data.SlWxProductTypeTabList
                })
            }
            
        },
        // 跳转列表页
        listName(id){
            this.$router.push('/shopList/'+id)
        }
    },
    created() {
        this.$store.dispatch('headTitle','分类')
    },
    mounted() {
        this.loading = this.$toast.loading();
        classifyNav(0).then(response=>{
            this.loading.hide();
            this.classify=response.data.data.SlWxProductTypeTabList
            this.classifyList=response.data.data.ProductTypeList
        })
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前你怕吗
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
// 侧边栏
    .classify{
        height:1334px;
        padding-top:90px;
        display: flex;
    }
    .slide{
        width:24%;
        height:100%;
        background:rgb(241,242,246);
        overflow-y: auto;
    }
    .slide-list{
        height:120px;
        font-size:12px;
        color:#000;
        text-align:center;
        line-height:120px;
        position: relative;
    }
    .slide-active{
        background:#fff;
        font-weight:bold;
    }
    .slide-border{
        width:6px;
        height:20px;
        display:block;
        position: absolute;
        left:0;
        top:50px;
        background:#f00;
        border-radius:10px;
    }
// 内容
.classify-inner{
    padding-top:60px;
    overflow: auto;
    display:flex;
    flex-wrap:wrap;
    width:76%;
    align-content: flex-start;
    background:#fff;
}
.classify-inner-list{
    width:190px;
    margin-bottom:70px;
}
.classify-img{
    width:190px;
    height:190px;
    display:block;
    border-radius:4px;
    margin-bottom:30px;
}
.classify-name{
    width:100%;
    text-align: center;
    font-size:12px;
    color:#000;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
</style>


